<template>
  <div class="config">
    <md-tabs md-fixed class="md-transparent">
        <md-tab id="model3" md-label="Model 3">
          <model3 v-model="cost"></model3>  
        </md-tab>
        <md-tab id="models" md-label="Model S">
          <models></models>
        </md-tab>
        <md-tab id="modelx" md-label="Model X">
          <modelx></modelx>
        </md-tab>
      </md-tabs>
      <bottom :cost="cost"></bottom>
    </div>
</template>

<script>
export default {
  name: 'config',
  data: () => ({
    cost: 0
  })
}
</script>



