<template>
    <div class="supercharger">
      <md-layout md-align="center">
        <md-table-card v-for="(row, rowIndex) in records" :key="rowIndex" style="margin: 10px; width: 70%">
            <md-toolbar>
                <h1 class="md-title">{{ rowIndex }}</h1>
            </md-toolbar>
        <md-table>
            <md-table-header>
                <md-table-row>
                <md-table-head style="text-align: center; width: 100px">Location</md-table-head>
                <md-table-head style="text-align: center; width: 100px">Parking Fee</md-table-head>
                <md-table-head style="text-align: center; width: 100px"># Chargers</md-table-head>
                <md-table-head style="text-align: center; width: 100px">Opening Hours</md-table-head>
                <md-table-head style="text-align: center; width: 100px">Since</md-table-head>
                <md-table-head style="text-align: center; width: 100px">Developer</md-table-head>
                </md-table-row>
            </md-table-header>

            <md-table-body>
                <md-table-row v-for="(col, colIndex) in row" :key="colIndex">
                    <md-table-cell>{{ col.location }}</md-table-cell>
                    <md-table-cell>{{ col.price }}</md-table-cell>
                    <md-table-cell>{{ col.number }}</md-table-cell>
                    <md-table-cell>{{ col.openhour }}</md-table-cell>
                    <md-table-cell>{{ col.opening }}</md-table-cell>
                    <md-table-cell>{{ col.developer }}</md-table-cell>
                </md-table-row>
            </md-table-body>
        </md-table>
        </md-table-card>
    </md-layout>
       
    </div>
</template>

<script>
import chargerData from '@/assets/supercharger.json'

export default {
  name: 'supercharger',
  data: () => ({
    records: chargerData
  })
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



