<template>
    <div class="clp">
        <md-layout md-align="center">
            <md-table>
                <md-table-header>
                    <md-table-row>
                    <md-table-head style="text-align: center; width: 100px">Location</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Available Chargers</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Quick Charger</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Semi Quick Charger</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Last Update</md-table-head>
                    </md-table-row>
                </md-table-header>

                <md-table-body>
                    <md-table-row v-for="(row, rowIndex) in CLPData[0]['.value']" :key="rowIndex">
                    <md-table-cell>{{row.Title}}</md-table-cell>
                    <md-table-cell><p v-if="row.TotalNumberOfAvailableCharger==0" style="color: red">{{row.TotalNumberOfAvailableCharger}} out of {{row.TotalNumberOfCharger}}</p>
                                            <p v-else style="color: green">{{row.TotalNumberOfAvailableCharger}} out of {{row.TotalNumberOfCharger}}</p></md-table-cell>
                    <md-table-cell v-if="row.TotalNumberOfQuickCharger==0">/</md-table-cell>
                    <md-table-cell v-else><p v-if="row.TotalNumberOfAvailableQuickCharger==0" style="color: red">{{row.TotalNumberOfAvailableQuickCharger}} out of {{row.TotalNumberOfQuickCharger}}</p>
                                            <p v-else style="color: green">{{row.TotalNumberOfAvailableQuickCharger}} out of {{row.TotalNumberOfQuickCharger}}</p></md-table-cell>
                    <md-table-cell v-if="row.TotalNumberOfSemiQuickCharger==0">/</md-table-cell>
                    <md-table-cell v-else><p v-if="row.TotalNumberOfAvailableSemiQuickCharger==0" style="color: red">{{row.TotalNumberOfAvailableSemiQuickCharger}} out of {{row.TotalNumberOfSemiQuickCharger}}</p>
                                            <p v-else style="color: green">{{row.TotalNumberOfAvailableSemiQuickCharger}} out of {{row.TotalNumberOfSemiQuickCharger}}</p></md-table-cell>
                    <md-table-cell>{{row.LastUpdate}}</md-table-cell>
                    </md-table-row>
                </md-table-body>
            </md-table>
        </md-layout>
    </div>
</template>

<script>
import Vue from 'vue'
import VueFire from 'vuefire'
import Firebase from 'firebase'

Vue.use(VueFire)

let config = {
  apiKey: 'AIzaSyCZq1D621JOgRz4XObrXCXpBqEdZvYbphA',
  authDomain: 'tesla-talk.firebaseapp.com',
  databaseURL: 'https://tesla-talk.firebaseio.com',
  projectId: 'tesla-talk',
  storageBucket: 'tesla-talk.appspot.com',
  messagingSenderId: '628266624996'
}

let app = Firebase.initializeApp(config)
let db = app.database()
let CLPDataRef = db.ref('CLPData')

export default {
  name: 'clp',
  firebase: {
    CLPData: CLPDataRef
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



